<template>
    <v-container>
        <v-layout row v-if="error">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>
                <v-card>
                    <v-card-text>
                        <h2 class="ml-3" style="color: darkgray; cursor: pointer" title="Se desejar podes actualizar a sua senha">
                            Actualize as disciplinas que registaste
                            <v-icon right dark class="green--text">check_circle</v-icon>
                        </h2>

                        <!--Informacao sobre nao necessidade de actualizar a senha para google-->
                        <v-card-title
                            class="red--text"
                        >
                            Ainda nao registaste nenhuma disciplina, registe as suas disciplinas favoritas e as que
                            explicas, pois os seus colegas vao te procurar !
                        </v-card-title>

                    </v-card-text>
                </v-card>

                <!-- Campos do formulario -->
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">

                                <!--Colocar aqui a lista das disciplinas do user-->
                                <!--Conteudo visualizado quando seleciona-se Subjects-->
                                <v-list class="">
                                    <v-flex xs12>
                                        <v-text-field
                                                name="description"
                                                label=""
                                                v-model="description"
                                                type="text"
                                                required></v-text-field>
                                    </v-flex>

                                    <div v-for="item in subjects">
                                        <v-divider></v-divider>
                                        <v-list-tile @click="description = item.name" class="pl-0 ml-0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>
                                                    {{ item.name }}
                                                </v-list-tile-title>
                                            </v-list-tile-content>
                                            <v-list-tile-action>

                                                <v-btn icon @click="description = item.name" ripple slot="activator">
                                                    <v-icon>edit</v-icon>
                                                </v-btn>

                                            </v-list-tile-action>
                                        </v-list-tile>
                                    </div>

                                </v-list>

                                <!--Botoes do formulario-->
                                <v-layout row>
                                    <v-flex xs12>

                                        <v-btn
                                            round
                                            @click=""
                                            >

                                            Cancelar
                                            <v-icon right dark class="red--text">highlight_off</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                        <v-btn
                                            round
                                            type="submit"
                                            :disabled="loading"
                                            :loading="loading">
                                            Gravar
                                            <v-icon right dark class="green--text">check_circle</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>
                                </v-layout>
                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                description: '',
            }
        },
        watch: {

            loading (value) {
                if (!value && !this.error) {

                    let text = "Disciplinas actualizadas com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.resetFields()
                }
            }

        },
        computed: {
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            hasSubjects () {
                return this.isUserAuth && this.userAuth.Educations && this.userAuth.Educations.subjects.length > 0
            },
            subjects () {
                return this.hasSubjects ? this.userAuth.Educations.subjects : []
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
        },
        methods: {
            onSignup () {
//                this.$store.dispatch('updateUserPassword', )
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onExplain (subject) {

                let user = this.userAuth
                let subjects = this.userAuth.Educations.subjects
                let finalSubjects = []
                subjects.forEach(function (s) {

                    if (s.name === subject.name) {

                        finalSubjects.push({
                            created_at: s.created_at,
                            name: s.name,
                            status_explain: !s.status_explain,
                            status_like: s.status_like,
                        })

                    } else {
                        finalSubjects.push(s)
                    }

                })

                user.Educations = {
                    institution: user.Educations.institution,
                    course: user.Educations.course,
                    subjects: finalSubjects,
                }

                this.$store.dispatch('updateOnlineUserData', user)

            },

            onLike (subject) {

                let user = this.userAuth
                let subjects = this.userAuth.Educations.subjects
                let finalSubjects = []
                subjects.forEach(function (s) {

                    if (s.name === subject.name) {

                        finalSubjects.push({
                            created_at: s.created_at,
                            name: s.name,
                            status_explain: s.status_explain,
                            status_like: !s.status_like,
                        })

                    } else {
                        finalSubjects.push(s)
                    }

                })

                user.Educations = {
                    institution: user.Educations.institution,
                    course: user.Educations.course,
                    subjects: finalSubjects,
                }

                this.$store.dispatch('updateOnlineUserData', user)

            },

        },

    }
</script>


